ss=\"icon-delete has-tooltip\" :title=\"t('nextframe', 'Delete')\" @click=\"$emit('delete', id)\" /></span>\n        </td>\n    </tr>\n</template>\n\n<script>\n\nimport { translate as t } from '@nextcloud/l10n'\n\nexport default {\n    name: 'NextframeItem',\n    components: {},\n    props: {\n        client: {\n            type: Object,\n            required: true,\n        },\n    },\n    data() {\n        return {\n            id: this.client.id,\n            name: this.client.name,\n            clientId: this.client.clientId,\n            ancestorUris: this.client.ancestorUri,\n            addAncestorUri: '',\n        }\n    },\n    computed: {},\n    methods: {\n        t,\n        addAncestor() {\n            this.$emit('addancestor', this.id, this.addAncestorUri)\n            this.addAncestorUri = ''\n        }\n    }\n}\n</script>\n\n<style scoped>\n    .icon-toggle,\n    .icon-delete {\n        display: inline-block;\n        width: 16px;\n        height: 16px;\n        padding: 10px;\n        vertical-align: middle;\n    }\n\n    td code {\n        display: inline-block;\n        vertical-align: middle;\n    }\n\n    table.inline td {\n        border: none;\n        padding: 5px;\n    }\n</style>\n"],"sourceRoot":""}]);
